@media screen and (max-width: 480px) {
  .container {
    max-width: 21rem;
    padding: 100px 20px;
  }
}

.footer {
  background-color: var(--primary-color);
}

.section-footer {
  padding-top: 32px;
  padding-bottom: 32px;
}

.footer-title {
  max-width: 300px;
  letter-spacing: -.02em;
  color: var(--first-color);
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.title-orange {
  color: var(--accent-color);
}

.footer-section-description {
  color: var(--first-color);
  margin-bottom: 14px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
}

.footer-list {
  flex-direction: column;
  row-gap: 4px;
  display: flex;
}

.footer-list-link {
  letter-spacing: -.02em;
  color: var(--first-color);
  transition: color var(--transition-text);
  font-size: 15px;
  font-style: normal;
  line-height: 1.29;
}

.footer-list-link:hover, .footer-list-link:focus {
  color: var(--accent-color);
}

.footer-social-link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-social-icon-mobile {
  stroke: var(--first-color);
  fill: rgba(0, 0, 0, 0);
}

.footer-social-link:focus .footer-social-icon-mobile, .footer-social-link:hover .footer-social-icon-mobile {
  stroke: var(--accent-color);
  animation: heartBeat 2s;
}

.address-social-wrapper {
  border-bottom: 1px solid rgba(255, 255, 255, .1);
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 32px;
  padding-bottom: 32px;
  display: flex;
}

.footer-social-list {
  column-gap: 14px;
  display: flex;
}

.footer-nav-list {
  flex-direction: column;
  row-gap: 18px;
  display: flex;
}

.footer-nav-link {
  letter-spacing: -.02em;
  color: var(--first-color);
  transition: color var(--transition-text);
  font-size: 14px;
  line-height: 1.14;
}

.footer-nav-link:hover, .footer-nav-link:focus {
  color: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .section-footer {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .footer-title {
    margin-bottom: 0;
    font-size: 28px;
    line-height: 1.21;
  }

  .footer-social-icon-mobile {
    width: 24px;
    height: 24px;
  }

  .desktop-tablet-container {
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    padding-bottom: 50px;
    display: flex;
  }

  .address-social-wrapper {
    border: none;
    column-gap: 50px;
    margin-bottom: 0;
    margin-left: auto;
    padding-bottom: 0;
  }

  .footer-social-list {
    column-gap: 18px;
  }

  .footer-nav-list {
    flex-direction: row;
    column-gap: 24px;
    padding-top: 24px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }

  .desktop-tablet-container {
    padding-bottom: 58px;
  }

  .address-social-wrapper {
    column-gap: 63px;
  }
}

